import Format from "../base/Format";
import { START_WEEK } from "../constants/CWC2023_DATETIME";
export default class ODI {
  constructor(
    id,
    date,
    team1,
    team2,
    venue,
    winner = null,
    odds1 = null,
    odds2 = null
  ) {
    this.id = id;
    this.date = date;
    this.team1 = team1;
    this.team2 = team2;
    this.venue = venue;
    this.winner = winner ? winner : null;
    this.odds1 = odds1;
    this.odds2 = odds2;
  }

  get ut() {
    return this.date.getTime() / 1000.0;
  }

  // Basic
  get weekAbsolute() {
    const SECONDS_IN_DAY = 86_400;
    return parseInt((this.ut + 3 * SECONDS_IN_DAY) / (7 * SECONDS_IN_DAY));
  }

  get week() {
    return this.weekAbsolute - START_WEEK + 1;
  }

  get title() {
    return `${this.team1.label} vs ${this.team2.label}`;
  }

  get hasWinner() {
    return !!this.winner;
  }

  // Probabilities
  get p1Winner() {
    return this.team1.pWinner / (this.team1.pWinner + this.team2.pWinner);
  }

  get p2Winner() {
    return this.team2.pWinner / (this.team1.pWinner + this.team2.pWinner);
  }

  // Odds
  get p1Odds() {
    return 1 / this.odds1 / (1 / this.odds1 + 1 / this.odds2);
  }

  get p2Odds() {
    return 1 / this.odds2 / (1 / this.odds1 + 1 / this.odds2);
  }

  get hasOdds() {
    return this.odds1 !== null && this.odds2 !== null;
  }

  // Combine Probabilities and Odds
  get p1() {
    if (this.hasWinner) {
      return this.winner === this.team1 ? 1 : 0;
    }
    if (this.hasOdds) {
      return this.p1Odds;
    }
    return this.p1Winner;
  }

  get p2() {
    if (this.hasWinner) {
      return this.winner === this.team2 ? 1 : 0;
    }
    if (this.hasOdds) {
      return this.p2Odds;
    }
    return this.p2Winner;
  }

  // Predict Winner
  get randomWinner() {
    if (this.winner) {
      return this.winner;
    }
    const f = (x) => x + 0.5;
    const q = f(this.p1) / (f(this.p1) + f(this.p2));
    return Math.random() < q ? this.team1 : this.team2;
  }

  get maximumLikelihoodWinner() {
    if (this.winner) {
      return this.winner;
    }
    return this.p1 > this.p2 ? this.team1 : this.team2;
  }

  get minimumLikelihoodWinner() {
    if (this.winner) {
      return this.winner;
    }
    return this.p1 > this.p2 ? this.team2 : this.team1;
  }

  get isConcluded() {
    return !!this.winner;
  }

  // Favourites and Underdogs
  get favoriteTeam() {
    return this.p1 > this.p2 ? this.team1 : this.team2;
  }

  get underdogTeam() {
    return this.p1 > this.p2 ? this.team2 : this.team1;
  }

  // Other

  getColor(winner) {
    const pWinner = winner === this.team1 ? this.p1 : this.p2;
    return Format.getPercentColor(pWinner);
  }

  getP(team) {
    if (team === this.team1) {
      return this.p1;
    }
    if (team === this.team2) {
      return this.p2;
    }
    throw new Error("Invalid team");
  }

  static groupByWeek(odiList) {
    return odiList.reduce(function (weekToODIList, odi) {
      const week = odi.week;
      if (!weekToODIList[week]) {
        weekToODIList[week] = [];
      }
      weekToODIList[week].push(odi);
      return weekToODIList;
    }, {});
  }
}
